/* -------------------------------------
   HABERLER — kurumsal beyaz tasarım
------------------------------------- */

.haberler-page {
  max-width: 1150px;
  margin: 0 auto;
}

.haberler-page h1 {
  font-size: 30px;
  font-weight: 700;
  margin-bottom: 32px;
  color: #2c3a2f;
}

/* Grid: profesyonel üçlü yapı */
.haberler-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  gap: 32px;
  margin-top: 24px;
}

.haberler-grid.single {
  display: flex;
  justify-content: center;
}

.haberler-grid.single .haber-card {
  max-width: 900px;
  width: 100%;
}

/* Alt mesaj */
.no-more-news {
  text-align: center;
  margin: 56px 0 80px;
  color: #777;
  font-size: 14px;
  font-style: italic;
}

/* Kart */
.haber-card {
  background: #ffffff;
  border-radius: 14px;
  overflow: hidden;
  border: 1px solid rgba(0,0,0,0.06);
  box-shadow: 0 8px 22px rgba(0,0,0,0.06);
  transition: all 0.22s ease;
  display: flex;
  flex-direction: column;
}

/* Hover efekti */
.haber-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 14px 26px rgba(0,0,0,0.09);
}

/* Görsel */
.haber-img {
  width: 100%;
  height: 300px;
  object-fit: cover;
  object-position: center;
  border-radius: 12px 12px 0 0;
}


/* İçerik */
.haber-card h2 {
  font-size: 18px;
  font-weight: 600;
  margin: 14px 18px 6px;
  color: #2f4034;
}

.haber-aciklama {
  font-size: 14px;
  color: #555;
  margin: 0 18px 8px;
}

.haber-tarih {
  font-size: 12px;
  color: #888;
  margin: 0 18px 12px;
}

/* Devamını Oku link */
.haber-link {
  margin: 0 18px 18px;
  color: #4a7c59;
  text-decoration: none;
  font-weight: 600;
  margin-top: auto;
}

.haber-link:hover {
  text-decoration: underline;
}

/* Mobil uyum */
@media (max-width: 700px) {
  .haber-img {
    height: 160px;
  }
}
